@use 'variable' as v;
@use '../base/mixins' as m;

.seccion {
    margin-top: calc(v.$separacion / 2);
    margin-bottom: calc(v.$separacion / 2);
}
.contenido-centrado {
    max-width: 80rem;
}

.boton-violeta{
    border-radius: .5rem;
    @include m.boton(v.$violeta, inline-block);


}

.boton-violeta-block{
    border-radius: .5rem;
    @include m.boton(v.$violeta, block);
}

.boton-amarillo{
    border-radius: .5rem;
    @include m.boton(v.$amarillo, inline-block);
}

.boton-rojo-block{
    border-radius: .5rem;
    @include m.boton(v.$rojo, block);
    width: 95%;
    padding: 1.5rem 3rem;
}

.boton-gris{
    border-radius: .5rem;
    @include m.boton(v.$grisOscuro, inline-block);


}

.boton-negro{
    border-radius: .5rem;
    @include m.boton(v.$negro, inline-block);
}

.boton-verde-block{
    border-radius: .5rem;
    @include m.boton(v.$verde, block);
}