@use '../base/mixins' as m;
@use '../base/variable' as v;


.contenedor-anuncios{
    @include m.tablet  {
        @include m.grid(3, 1fr);
        gap: 2rem;
            
        
    }

    
}

.contenido-anuncio{
    padding: 3rem;
    background-color: v.$blanco;
    border: 1px solid darken(v.$blanco, 10);
    margin-bottom: 3rem;
    border-bottom-left-radius: 3rem;

    h3{text-transform: uppercase;
        color: lighten(v.$negro, 35%);
        font-weight: bold;}
    h3, p{margin: 0;}

    a{
        border-bottom-left-radius:2rem ;
    }

}

.precio{color: v.$verde;
    font-weight: bold;
    font-size: 3rem;}
    
.iconos-caracteristicas{
    display: flex;
    padding: 0;
    list-style: none;
    
    li{
        flex: 1;
        display: flex;

        img{
            flex: 0 0 3rem;
            margin-right: 1rem;}
        
        p{ font-weight: bold;}    
    }
}

.propiedad{

    @include m.tablet {
        @include m.grid(2, 5rem);
            
        
    }

   

    .valor{
        font-size: 3rem;
    }

    .boton{
        margin-top: 5rem;
    }
}

.titulo-propiedad{
    font-weight: bold;
}

.propiedad__imagen{
    
    margin: auto;
    img{
    border-radius: 1rem;

    -webkit-box-shadow: 4px 2px 9px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 4px 2px 9px 0px rgba(0,0,0,0.75);
box-shadow: 4px 2px 9px 0px rgba(0,0,0,0.75);
}}

.propiedad__icono{
    margin-right: 2rem;
    padding: 1rem;
    width: 10rem;
}

.descripcion{
    line-height: 3rem;
    text-align: justify;
}

.caracteristicas{
    align-self: baseline;
    padding: .4rem 1rem .4rem 1rem;
    border-radius: .5rem;
    border: 1px solid v.$grisOscuro;
    -webkit-box-shadow: 2px 2px 1px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 2px 2px 1px 0px rgba(0,0,0,0.75);
box-shadow: 2px 2px 1px 0px rgba(0,0,0,0.75);
}