  display: none;
}

.no-webp .inicio {
  background-image: url(../img/header.jpg);
}

.webp .inicio {
  background-image: url(../img/header.webp);
}

.header {
  background-color: #333333;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 60rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 60rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  color: #FFFFFF;
  font-weight: bold;
  line-height: 2;
  max-width: 50rem;
  text-align: left;
}

.barra {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    padding-top: 3rem;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-between;
  }
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 2rem 0;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
    margin: 0;
  }
}
.derecha .dark-mode {
  width: 3rem;
  filter: invert(100%);
}
.derecha .dark-mode:hover {
  cursor: pointer;
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    visibility: visible;
    opacity: 1;
    padding-top: 2rem;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  display: block;
  text-align: center;
  font-size: 2.2rem;
  position: relative;
  transition: all 0.5s ease;
}
.navegacion a::before {
  content: "";
  bottom: 0;
  left: 0;
  position: absolute;
  width: 0;
  height: 0.3rem;
  background-color: #E08709;
  transition: all 0.5s ease;
}
.navegacion a:hover {
  color: #E08709;
}
.navegacion a:hover::before {
  width: 100%;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.mobile_menu {
  width: 5rem;
}
@media (min-width: 768px) {
  .mobile_menu {
    display: none;
  }
}

.mostrar {
  opacity: 1;
  visibility: visible;
  height: auto;
}

.footer {
  background-color: #333333;
  margin-bottom: 0;
  padding: 3rem;
}

.contenedor-footer {
  padding-top: 3rem;
  display: flex;
  justify-content: space-around;
}
.contenedor-footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
  padding-top: 0;
}

.copyright {
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.icono {
  box-shadow: -1px 2px 40px -20px rgba(0, 0, 0, 0.75);
  -webkit-box-shadow: -1px 2px 40px -20px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: -1px 2px 40px -20px rgba(0, 0, 0, 0.75);
  text-align: center;
  border-radius: 1rem;
  background-color: #FFFFFF;
  padding: 2rem;
}
.icono h3 {
  text-transform: uppercase;
  margin-top: 0;
  font-weight: 700;
  color: #595959;
}
.icono img {
  height: 10rem;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 1fr;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.contenido-anuncio {
  padding: 3rem;
  background-color: #FFFFFF;
  border: 1px solid #e6e6e6;
  margin-bottom: 3rem;
  border-bottom-left-radius: 3rem;
}
.contenido-anuncio h3 {
  text-transform: uppercase;
  color: #595959;
  font-weight: bold;
}
.contenido-anuncio h3, .contenido-anuncio p {
  margin: 0;
}
.contenido-anuncio a {
  border-bottom-left-radius: 2rem;
}

.precio {
  color: #6a9919;
  font-weight: bold;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  padding: 0;
  list-style: none;
}
.iconos-caracteristicas li {
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.iconos-caracteristicas li p {
  font-weight: bold;
}

@media (min-width: 768px) {
  .propiedad {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.propiedad .valor {
  font-size: 3rem;
}
.propiedad .boton {
  margin-top: 5rem;
}

.titulo-propiedad {
  font-weight: bold;
}

.propiedad__imagen {
  margin: auto;
}
.propiedad__imagen img {
  border-radius: 1rem;
  -webkit-box-shadow: 4px 2px 9px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 4px 2px 9px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 4px 2px 9px 0px rgba(0, 0, 0, 0.75);
}

.propiedad__icono {
  margin-right: 2rem;
  padding: 1rem;
  width: 10rem;
}

.descripcion {
  line-height: 3rem;
  text-align: justify;
}

.caracteristicas {
  align-self: baseline;
  padding: 0.4rem 1rem 0.4rem 1rem;
  border-radius: 0.5rem;
  border: 1px solid #333333;
  -webkit-box-shadow: 2px 2px 1px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 2px 2px 1px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 2px 2px 1px 0px rgba(0, 0, 0, 0.75);
}

.no-webp .imagen-contacto {
  background-image: url(../img/encuentra.jpg);
}

.webp .imagen-contacto {
  background-image: url(../img/encuentra.webp);
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h3 {
  font-size: 4rem;
  color: #FFFFFF;
  font-weight: bold;
}
.imagen-contacto p {
  font-size: 1.8rem;
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}

.entrada_blog {
  margin-bottom: 2rem;
}
.entrada_blog:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .entrada_blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada_blog img {
  border-radius: 1rem;
}

.entrada_texto a {
  display: block;
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .entrada_texto a {
    margin-top: 0;
  }
}
.entrada_texto h4 {
  color: #000000;
  margin: 0;
}
.entrada_texto h4::after {
  content: "";
  width: 15rem;
  display: block;
  height: 0.5rem;
  background-color: #2e2344;
}
.entrada_texto p {
  color: #000000;
  font-weight: 700;
}
.entrada_texto span {
  color: #06568d;
  font-weight: bold;
}

.info-meta {
  font-weight: 700;
}
.info-meta span {
  color: #E08709;
}

.testimonio {
  background-color: #2e2344;
  color: #FFFFFF;
  padding: 2rem;
  border-radius: 2rem;
}
.testimonio blockquote {
  padding-left: 5rem;
  position: relative;
  line-height: 3.5rem;
}
.testimonio blockquote::before {
  position: absolute;
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  display: block;
  width: 6rem;
  height: 8rem;
  left: -2rem;
}

.entrevistado {
  color: #FFFFFF;
}

@media (min-width: 768px) {
  .nosotros {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.nosotros .nosotros_imagen img {
  border-radius: 1rem;
  -webkit-box-shadow: 2px 2px 9px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 2px 2px 9px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 2px 2px 9px 0px rgba(0, 0, 0, 0.75);
}

.nosotros_texto {
  margin-top: 3rem;
}
.nosotros_texto blockquote {
  font-weight: bold;
  font-size: 2.2rem;
  margin: 0;
  color: #06568d;
  padding-left: 1rem 0 3rem 0;
}

.formulario p {
  color: #333333;
  margin: 2rem 0 0 0;
  font-size: 1.4rem;
}
.formulario:has(:invalid) {
  --color: red;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
  font-weight: 700;
}
.formulario label {
  display: block;
  font-weight: bold;
  text-transform: uppercase;
}
.formulario input:not([type=submit]),
.formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  border: 1px solid #e1e1e1;
  background-color: #FFFFFF;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  width: auto;
  margin: 0;
}
.formulario input[type=submit] {
  margin-top: 2rem;
}

.dark_mode {
  background-color: #1a1a1a;
}
.dark_mode p {
  color: #FFFFFF;
}
.dark_mode a, .dark_mode h1, .dark_mode h2, .dark_mode h3, .dark_mode h4 {
  color: #e1e1e1;
}
.dark_mode .contenido-anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark_mode .precio {
  color: #6a9919;
}
.dark_mode .iconos-caracteristicas img {
  filter: invert(100%);
}
.dark_mode .nosotros_texto blockquote {
  color: #e1e1e1;
}
.dark_mode .formulario label, .dark_mode .formulario legend {
  color: #FFFFFF;
}
.dark_mode .formulario input:not([type=submit]),
.dark_mode .formulario textarea, .dark_mode .formulario select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}
.dark_mode table.propiedades thead {
  color: #FFFFFF;
}
.dark_mode table.propiedades td {
  color: #FFFFFF;
}
.dark_mode table.propiedades tbody tr:nth-child(odd) {
  background-color: black;
}
.dark_mode .icono {
  background-color: #262626;
}
.dark_mode .entrada_texto h4::after {
  background-color: #6a9919;
}
.dark_mode .entrada_texto span {
  color: #E08709;
}
.dark_mode .nosotros_texto blockquote {
  color: #E08709;
}
.dark_mode .tabs .actual {
  background-color: #FFFFFF;
  color: #000000;
}
.dark_mode .caracteristicas {
  color: #000000;
  background-color: #FFFFFF;
  border: 1px solid #FFFFFF;
  -webkit-box-shadow: 2px 2px 1px 0px rgba(221, 221, 221, 0.7490196078);
  -moz-box-shadow: 2px 2px 1px 0px rgba(221, 221, 221, 0.7490196078);
  box-shadow: 2px 2px 1px 0px rgba(221, 221, 221, 0.7490196078);
}

.alerta {
  padding: 1rem;
  text-transform: uppercase;
  margin: 1rem 0;
  color: #FFFFFF;
  text-align: center;
  font-weight: 700;
}
.alerta.error {
  background-color: rgb(182, 26, 26);
}
.alerta.exito {
  background-color: #6a9919;
}
.alerta.actualizado {
  background-color: #E08709;
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
  text-align: center;
}
table.propiedades thead {
  background-color: #6a9919;
}
